body {
  margin: 0;
  font-family: 'Nunito','Fira Sans', 'monospace', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Nunito','Fira Sans', 'monospace', source-code-pro, Menlo, Monaco, Consolas, 'Courier New';
}

.header {
    display: flex;
    justify-content: space-between;
    flex: 1 1;
    padding: 12px;
    padding-top: 20px;
    padding-bottom: 20px;
    position: relative;
    align-items: center;
    background-color: rgba(85,26,139,255)
}

.menu {
    display: flex;
    flex: 1 1;
    justify-content: flex-end;
    align-items: center;
}

.menu-icon {
    font-size: 26px;
}

.mobile-menu {
    display: none;
}

.web-menu {
    display: flex;
}

.name {
    display: flex;
    font-size: 20px;
    color: white;
}

a.name {
    text-decoration: none;
    color: white;
}

@media only screen and (max-width: 720px) {
.mobile-menu{
    display: flex;
}
.web-menu{
    display: none;
}
.name {
    font-size: 18px;
    color: white;
}
}


.web {
    display: flex;
    flex: 1 1;
    align-items: center;
}

.web-option {
    font-size: 20px;
    margin-right: 32px;
    font-weight: 600;
}

.web-option a {
    text-decoration: none;
    color: rgb(255, 255, 255);
    display: flex;
    align-items: center;
}
.mobile {
    height: 100vh;
    width: 60vw;
    position: absolute;
    right: 0px;
    padding: 30px 12px;
    top: -6px;
    display: flex;
    flex-direction: column;
    background-color: white;
}

.close-icon {
    font-size: 20px;
    display: flex;
    justify-content: flex-end;
}

.mobile-options {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-top: 0px;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.mobile-option {
    font-size: 20px;
    margin: 36px 8px;
    display: flex;
}

.mobile-option a {
    text-decoration: none;
    color: black;
    display: flex;
}
.body {
    padding: 12px;
    padding-bottom: 36px;
}

p {
    font-size: 18px;
}
.social-contact {
    display: flex;
    flex-direction: row;
    padding-top: 20px;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    grid-gap: 30px;
    gap: 30px;
}

.social-icon {
    color: rgba(85,26,139,255)
}

a.social-icon {
    margin: 0 1rem;
    transition: -webkit-transform 250ms;
    transition: transform 250ms;
    transition: transform 250ms, -webkit-transform 250ms;
    display: inline-block;
  }

a.social-icon:hover {
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

@media only screen and (max-width: 370px) {
    .social-contact {
        grid-gap: 15px;
        gap: 15px;
    }
}
.about {
  padding-bottom: 48px;
}

.about-top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.picture {
  height: 320px;
  padding: 10px;
}

.about-info {
  font-size: 22px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  grid-gap: 14px;
  gap: 14px;
}

.education {
  font-style: italic;
  color: rgb(169, 91, 169);
}

.info-name {
  font-size: 54px;
  font-weight: 600;
  font-family: 'Fira Mono', 'Fira Sans', monospace;
  color: rgba(85,26,139,255)
}

.hello {
  font-family: 'Fira Mono', 'Fira Sans', monospace;
  font-weight: 500;
}

.statement {
  padding-top: 24px;
  font-family: 'Fira Mono', 'Fira Sans', monospace;
  font-weight: 500;
}

@media only screen and (max-width: 1096px) {
  .info-name {
    font-size: 38px;
  }
}


@media only screen and (max-width: 720px) {
  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .info-name {
    font-size: 34px;
  }
  .statement {
    font-size: 18px;
  }
  .hello {
    font-size: 18px;
  }
  .education {
    font-size: 18px;
  }
}

@media only screen and (max-width: 430px) {
  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .picture {
    height: 300px;
  }
  
}

@media only screen and (max-width: 430px) {
  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .picture {
    height: 300px;
  }
  .info-name {
    font-size: 34px;
    font-weight: 600;
    font-family: 'Fira Mono', 'Fira Sans', monospace;
    color: rgba(85,26,139,255)
  }
}

@media only screen and (max-width: 360px) {
    .about-top {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    .picture {
      height: 250px;
    }
  }




 

.project-card-container {
    scroll-behavior: smooth;
    display: flex;
    flex-direction: column;
    min-height: 450px;
    max-width: 80%;
    align-items: center;
}

.project-card {
    flex: 1 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 16px 0px;
    padding: 15px 15px;
    border: 3px solid rgb(212, 194, 212);
    border-radius: 8px;

}

.project-card:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    transition: all 0.5s ease;
    box-shadow: 0 10px 40px 0 rgba(40, 39, 39, 0.4);
    z-index: 3;
}

.project-title{
    font-size: 24px;
    font-weight: 600;
}

.project-photo {
    width: 40%;
    max-height: 80%;
    max-width: 95%;
}

.project-tags {
    display: flex;
    max-width: 90%;
    flex-wrap: wrap;
    margin-bottom: 16px;
    margin-top: 16px;
}

.tag {
    padding: 4px 8px;
    background-color: rgb(125, 81, 125);
    color: white;
    border-radius: 8px;
    margin-right: 8px;
    margin-bottom: 4px;
}

.fi-brands-github {
    margin-right: 5px;
}

.fi-rr-globe {
    margin-right: 5px;
}

.project-links a {
    text-decoration: none;
    color: black;
}

.project-links {
    display: flex;
    flex-direction: row;
}

.link-button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px 0px;
    font-size: 16px;
    margin-right: 12px;
}

.link-button i {
    display: flex;
    align-items: center;
    -webkit-mask-repeat: 4px;
            mask-repeat: 4px;
}

.section-title {
    font-size: 40px;
    font-weight: 700;
    color: rgb(88, 38, 135);
}

@media only screen and (max-width: 720px) {
.project-photo {
    max-width: 70%;
}
.project-card {
    flex-direction: column;
    border: 3px solid rgb(205, 182, 207);
}
.project-info {
    margin-left: 0px;
    margin-top: 12px;
}
.project-tags {
    max-width: 95%;
}
.project-info {
    max-width: 95%;
}
}
.projects {
    display: flex;
    flex-direction: column;
    flex: 2 1;
}
.about-detail {
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
}
.section-title {
    margin-bottom: 10px;
}

p {
    margin: 10px 10px 10px 0px;
    line-height: 1.6;
}

html {
    scroll-behavior: smooth;
}
.home {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: auto;
}

@media only screen and (max-width: 1024px){
.home{
    width: 90%;
}
}

@media only screen and (max-width: 512px) {
.home{
    width: 100%;
}
}



